

.skillsWrapper {
    background-color:#343437;
}
.wrapper {
    height: 100%;
    max-width: 1250px;
    margin: 0 auto;
    padding:25px;
    display: flex;
    flex-direction: column;
   justify-content: flex-start;
    align-items: center;


}


.skills{
  width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 30px;
    gap: 20px;

}

.card{
    position: relative;
    cursor: pointer;
}
.card .face{
    margin-top: -40px;
    width: 200px;
    height: 150px;
    transition: 0.5s;

}
.card .face.face1{
    position: relative;
    background: #333;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1;
    transform: translateY(100px);
}

.card:hover .face.face1{
    background:  #F7D449;;
    transform: translateY(0);
}


.card:hover .icon{
    opacity: 1;
    
}

.card .face.face1 .content img{
    max-width: 100px;
}

.card .face.face1 .content h3{
    margin: 10px 0 0;
    padding: 0;
    color: #fff;
    text-align: center;
    font-size: 1.5em;
}

.card .face.face2{
    position: relative;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    box-sizing: border-box;
    box-shadow: 0 20px 50px rgba(0, 0, 0, 0.8);
    transform: translateY(-10px);
}

.card:hover .face.face2{
    transform: translateY(30px);
}

.card .face.face2 .content p{
    margin: 0;
    padding: 0;
}

.card .face.face2 .content a{
    margin: 15px 0 0;
    display:  inline-block;
    text-decoration: none;
    font-weight: 900;
    color: #333;
    padding: 5px;
    border: 1px solid #333;
}

.card .face.face2 .content a:hover{
    background: #333;
    color: #fff;
}

.icon{
    height: 70px;
    width: 70px;
    background-repeat: no-repeat;
    opacity: 0.2;
    transition: 0.5s;


}


